<template>
  <div class="share-buttons">
    <span class="text-subtitle-1 mr-2 grey--text">Share:</span>

    <!-- Facebook -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          icon
          color="#1877F2"
          @click="shareFacebook"
          v-bind="attrs"
          v-on="on"
          aria-label="Share on Facebook"
        >
          <v-icon>mdi-facebook</v-icon>
        </v-btn>
      </template>
      <span>Share on Facebook</span>
    </v-tooltip>

    <!-- X (Twitter) -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          icon
          color="black"
          @click="shareX"
          v-bind="attrs"
          v-on="on"
          aria-label="Post on X"
        >
          <!-- Custom X Logo SVG -->
          <svg
            viewBox="0 0 24 24"
            width="24"
            height="24"
            fill="currentColor"
            role="img"
          >
            <path
              d="M18.244 2.25h3.308l-7.227 8.26 8.502 11.24H16.17l-5.214-6.817L4.99 21.75H1.68l7.73-8.835L1.254 2.25H8.08l4.713 6.231zm-1.161 17.52h1.833L7.084 4.126H5.117z"
            ></path>
          </svg>
        </v-btn>
      </template>
      <span>Post on X</span>
    </v-tooltip>

    <!-- Copy Link -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          icon
          color="grey darken-2"
          @click="copyLink"
          v-bind="attrs"
          v-on="on"
          aria-label="Copy Link"
        >
          <v-icon>mdi-link-variant</v-icon>
        </v-btn>
      </template>
      <span>{{ copyTooltip }}</span>
    </v-tooltip>

    <v-snackbar v-model="snackbar" timeout="2000" color="success" bottom>
      Link copied to clipboard!
      <template v-slot:action="{ attrs }">
        <v-btn text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  name: "ShareButtons",
  props: {
    url: {
      type: String,
      required: true,
    },
    title: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      snackbar: false,
      copyTooltip: "Copy Link",
    };
  },
  methods: {
    shareFacebook() {
      const shareUrl = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(
        this.url
      )}`;
      window.open(shareUrl, "_blank", "width=600,height=400");
    },
    shareX() {
      // Twitter/X Intent
      const text = this.title;
      const shareUrl = `https://twitter.com/intent/tweet?text=${encodeURIComponent(
        text
      )}&url=${encodeURIComponent(this.url)}`;
      window.open(shareUrl, "_blank", "width=600,height=400");
    },
    async copyLink() {
      try {
        await navigator.clipboard.writeText(this.url);
        this.snackbar = true;
        this.copyTooltip = "Copied!";
        // Reset tooltip text after a delay
        setTimeout(() => {
          this.copyTooltip = "Copy Link";
        }, 2000);
      } catch (err) {
        console.error("Failed to copy link: ", err);
        // Fallback for older browsers or if navigator.clipboard is blocked
        this.fallbackCopyTextToClipboard(this.url);
      }
    },
    fallbackCopyTextToClipboard(text) {
      const textArea = document.createElement("textarea");
      textArea.value = text;
      
      // Ensure it's not visible but part of DOM
      textArea.style.position = "fixed";
      textArea.style.left = "-9999px";
      document.body.appendChild(textArea);
      textArea.focus();
      textArea.select();

      try {
        const successful = document.execCommand('copy');
        if (successful) {
           this.snackbar = true;
           this.copyTooltip = "Copied!";
           setTimeout(() => {
             this.copyTooltip = "Copy Link";
           }, 2000);
        } else {
           alert("Unable to copy link.");
        }
      } catch (err) {
        console.error('Fallback: Oops, unable to copy', err);
        alert("Unable to copy link.");
      }

      document.body.removeChild(textArea);
    }
  },
};
</script>

<style scoped>
.share-buttons {
  display: flex;
  align-items: center;
  gap: 8px; /* Add some space between icons */
}
</style>
